.gallery--standard {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.subgallery {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(5,1fr);
  grid-gap: 10px;
  margin-top: 20px;
}

.subgallery__item--clickable {
  cursor: pointer;
}

.subgallery figure  {
  width: 100%;
}

.subgallery figcaption  {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
}

.subgallery__image {
  width: 100%;
}

.gallery__desc {
  -ms-flex: 0 0 50%;
      flex: 0 0 50%;
}

.gallery__title {
  -ms-flex: 0 0 50%;
      flex: 0 0 50%;
}

@media only screen and (max-device-width: 768px) {
  .subgallery {
    grid-template-columns: repeat(1,1fr);
  }

  .gallery__desc {
    -ms-flex: 1 0 100%;
        flex: 1 0 100%;
  }

  .gallery__title {
    -ms-flex: 1 0 100%;
        flex: 1 0 100%;
  }

}



